body {
  text-align: center;
  font-family: Tahoma, Geneva, Verdana, sans-serif;
  /* background: radial-gradient(ellipse at top, lightblue, transparent),
    radial-gradient(ellipse at bottom, white, transparent); */
}
.space-between {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
#input {
  font-weight: bold;
}
/* .text-white {
  color: white;
}

.text-black {
  color: black;
} */
.bill-container {
  display: flex;
  flex-direction: row;
  padding: 20px;
}
.bill-left-container {
  background: radial-gradient(ellipse at top, lightgrey, transparent),
    radial-gradient(ellipse at bottom, lightblue, transparent);
  min-height: 80vh;
  flex: 1;
  padding: 20px;
  border-radius: 10px 0px 0px 10px;
}
.bill-right-container {
  background: radial-gradient(ellipse at top, lightgrey, transparent),
    radial-gradient(ellipse at bottom, lightblue, transparent);
  min-height: 80vh;
  flex: 1;
  padding: 20px;
  border-radius: 0px 10px 10px 0px;
}
.item-card {
  margin: 10px;
  border: 1px solid;
}
.App {
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (max-width: 680px) {
  .bill-container {
    display: flex;
    flex-direction: column;
    height: 100px;
    padding: 20px;
  }
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
